import Vue from 'vue'
import VueX from 'vuex'

Vue.use(VueX)

export default new VueX.Store({
  state: {
    unit: null
  },
  mutations: {
    setUnit(state, unit) {
      state.unit = unit
    }
  },
  actions: {
    setUnit ({commit}, unit) {
      commit('setUnit', unit)
    }
  }
})


