<template>
  <v-container fluid>
    <v-layout row justify-space-between>
      <v-flex xs6>
        <v-select
          v-bind:items="unit"
          v-model="unitFrom"
          label="Select"
          single-line
          bottom
        ></v-select>
      </v-flex>
      <v-flex xs6 offset-xs1>
        <v-select
          v-bind:items="unit"
          v-model="unitTo"
          label="Select"
          single-line
          bottom
        ></v-select>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs6>
        <v-text-field
          label="From"
          v-model="valueFrom"
        ></v-text-field>
      </v-flex>
      <v-flex xs6 offset-xs1>
        <v-text-field
          label="To"
          :label="valueTo"
          disabled
        ></v-text-field>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      unitFrom: this.$store.state.unit[0],
      unitTo: this.$store.state.unit[0],
      valueFrom: null,
      valueTo: null
    }
  },
  computed: {
    unit () {
      return this.$store.state.unit
    }
  },
  watch: {
    valueFrom () {
      if (!isNaN(this.valueFrom)) {
        this.valueTo = this.convert()
      }
    },
    unitFrom () {
      if (!isNaN(this.valueFrom)) {
        this.valueTo = this.convert()
      }
    },
    unitTo () {
      if (!isNaN(this.valueFrom)) {
        this.valueTo = this.convert()
      }
    },
  },
  methods: {
    convert () {
      return (this.unitTo.val * this.valueFrom / this.unitFrom.val).toString()
    }
  }
}
</script>

<style>

</style>



